div.tech{
    text-align: center;
    margin-bottom:6%;
}

h1.techHead{
    color:#FE7877; 
    margin-top:4%;
    margin-bottom:3%;
    font-size: 40px;
  
}

.upper-div {
    width: 70%;
    display: block;
    margin: 0 auto;
    background-color: whitesmoke;
    border-radius: 3px;
    height: 18px;
    margin-top: 1.9%;
    box-shadow: 0 0 2px 1px cornflowerblue;
    text-align: left;
   
}

.skills-div > span {
    color: #fff;
    text-transform: uppercase;
    font-size: medium;
    font-family: Cabin, 'sans-serif';
    font-weight: bold;
    margin-left:10px;
}

.skills-div {
    height: 18px;
    border-radius: 3px;
}

