body {
  margin: 0;
  padding: 0;
  font-family:  "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


div.headerStyle{
    background-color: #313244;
    text-align: center;
    border-top: 5px solid #808080;
   
    -webkit-clip-path: polygon(
        0% 0%,         
        100% 0%,         
        100% 200%,         
        0% 85%      
      );
   
            clip-path: polygon(
        0% 0%,         
        100% 0%,         
        100% 200%,         
        0% 85%      
      );
}

div.headerStyle .col-3{
    padding-left: 0;
}



img.headerImage {
    width:100%;
    height:100%;
    opacity:0.1;
}

.img-fluid{
    max-width: 100%;
    height:auto;
}

h1.myName{
    font-family: Poppins,Helvetica,Arial,sans-serif;
    color:black;
    font-weight: bold;
    font-size:8vw;
    margin-top:8%;
}

p.titleStyle{
    font-size:4vw;
    color:black;
    font-weight: bold;
    font-family: Poppins,Helvetica,Arial,sans-serif;
}


.more-button{
   background:#313244;
   color:#FE7877;
   font-size:2.5vw;
   font-weight: 500;
   margin-top:7%;
   margin-bottom:4%;
   margin-right:5px;
   border-radius: 2%;
   border: 1px solid #fff;
   box-shadow: 0 0 1px #fff;
}
.more-button:hover{
    background-color:#FE7877;
    color:#313244;
    font-weight:bold;
}

p{text-align: center;}


.navbar-nav .nav-link {
    
    float: right;
    margin-right: 12px;
    letter-spacing: 3px;
    font-size: 15px;
    
}

.nav-link { 
    color:#FE7877 !important;
    opacity:0.7;
}

.nav-link:hover{
    text-decoration: underline;
    opacity:1;
}

.navbar-toggler {
   
    font-size: 2vw;
    line-height: 1;
    border: 1.5px solid #fff !important;
    border-radius: .25rem;
    background: #FE7877;
    opacity: 0.8;
    margin-top:5px;
}

.navbar-toggler:hover{
    background: #FE7877;
   
}

.aboutCard {
    margin-top: 100px;
    margin-left: 20%;
    box-shadow: 0px 0px 3px 2.3px #505050;
    background-color: #F6F6F5;
    margin-bottom:100px;
}

.abtTitle{
    font-size: 37px;
    color: #FE7877;
    text-align: center;
}

.card-text:last-child {
    line-height: 1.8;
    color: #313244;
    font-size: 22px;
}

span.interest {
    font-weight: bold;
}
div.tech{
    text-align: center;
    margin-bottom:6%;
}

h1.techHead{
    color:#FE7877; 
    margin-top:4%;
    margin-bottom:3%;
    font-size: 40px;
  
}

.upper-div {
    width: 70%;
    display: block;
    margin: 0 auto;
    background-color: whitesmoke;
    border-radius: 3px;
    height: 18px;
    margin-top: 1.9%;
    box-shadow: 0 0 2px 1px cornflowerblue;
    text-align: left;
   
}

.skills-div > span {
    color: #fff;
    text-transform: uppercase;
    font-size: medium;
    font-family: Cabin, 'sans-serif';
    font-weight: bold;
    margin-left:10px;
}

.skills-div {
    height: 18px;
    border-radius: 3px;
}

.html {
    background-color:#E54D27;
    width: 93%;
}

.css{
    background-color:#016FB8;
    width: 91%;
}

.javascript{
    background-color: darkseagreen;
    width: 84%;
}

.bootstrap{
    background-color: #563D7B;
    width: 88%;
}

.react{
    background-color:#63DBFB;
    width:90%;
}

.redux{
    background-color: #764ABC;
    width:86%;
}

.es6{
    background-color:darkseagreen;
    width:88%;
}

.c{
    background-color: #649AD2;
    width:40%; 
}

.java{
    width: 55%;
    background-color:#5381A1;
}

.mysql{
    background-color: #F8971D;
    width: 88%;
}

.csharp {
    background-color: #953DAC;
    width:25%; 
}

.rails{
    background-color: #8D262D;
    width:40%;
}

.php{
    background-color: #333;
    width:45%;
}

.git{
    background-color: #F15033;
    width:90%;
}

.linux{
    background-color:#020202;
    width:93%;
}



div.TimeStyle{
   
    -webkit-clip-path: polygon( 0% 0%, 88% 0%, 632% 223%, 0% 97% );
   
            clip-path: polygon( 0% 0%, 88% 0%, 632% 223%, 0% 97% );
          
          background-color: #FE7A7B;
          background-color: #fec8c8;
          background-color: #E3BF67;
          background-color: #313244;
    
}

p.certHead{
    color:#FE7877;
    text-align: center;
    margin-top:4%;
    margin-bottom:3%;
    font-size: 40px;
    
}

.vertical-timeline::before{
    background: #fff;
}

.vertical-timeline-element-content{
    color:#313244;
    background: #fff;
    
}

.vertical-timeline-element-content > p{
    color:#313244;
}

span.vertical-timeline-element-date{
    color: #fff !important;
    margin-left: 45px;
    margin-right: 45px;
}


@media only screen and (max-width: 1180px) {

    span.vertical-timeline-element-date{
        color: #333333 !important;
       
    }
    

  }
.project-style{
    background-color: #313244;
    text-align: center;

    -webkit-clip-path: polygon( 0% 0%, 87% 0%, 534% 278%, 0% 95% );

            clip-path: polygon( 0% 0%, 87% 0%, 534% 278%, 0% 95% );

    padding-bottom:10%;
}

p.projectHead{
    color:#FE7877;
    margin-top:4%;
    margin-bottom:3%;
    font-size: 40px;
}

img.project-img{
    width:450px;
    height:250px;
    border:5px solid #fff;
    margin-bottom:17px;
    border-radius: 4px;
}


img.project-img:hover{
    -webkit-transform: scale(1.11);
            transform: scale(1.11);
    -webkit-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
   
}

.img-fluid{
    max-width: 100%;
    height:auto;
}


    

p.contactHead{
    color:#FE7877;
    font-size: 40px;
    margin-top:3%;
    margin-bottom:2%;
}

div.contact-design{
  text-align: center;
}

.form-design{
    width:60%;
    margin:auto;
}

.form-group{
    font-family: 'Cabin' , sans-serif;
    margin-bottom:30px;
}

input.form-control{
    font-size: 20PX;
    background: #f5f5f5;
    color:#313244;
}

textarea.form-control{
    font-size: 20px;
    background: #f5f5f5;
    color:#313244;
}

.form-button{
   width:200px;
   background-color:#313244;
   margin-top:1%;
   margin-bottom:15%;
   font-size:25px;
   
}

.form-button:hover{
    background-color:#FE7877;
    color:#313244;
    font-weight:bold;
}



    


div.footer-style{
    background-color:#313244;
    text-align: center;
   
}

p.learn-more{
    color:#fff;
    margin-top:5%;
    font-size:12px;
}

.social{
   background:#313244;
   color:#FE7877;
   border-radius: 0%;
   font-size:20px;
   font-weight: 500;
   margin-top:0.2%;
   margin-bottom:4%;
   margin-right:5px;
   border-radius: 5%;
   border: 1px solid #fff;
   box-shadow: 0 0 1px #fff;
}

.social:hover{
    background-color:#FE7877;
    color:#313244;
    font-weight:bold;
}

div.address{
    color:#fff;
    margin-top:4%;
    font-size:20px;
    
}

div.address > span{
    margin-bottom:2%;
}

div.phone{
    color:#fff;
    margin-top:4%;
    font-size:20px;
}

div.phone > span{
    margin-bottom:2%;
}

p.madeBy{
    color:#fff;
    font-size:12px;
    margin-top:2%;
    margin-bottom: 2%;
}

.madeBy > span{
    color:#FE7877;
}

.back{
    float:right;
    background:#313244;
    color:#FE7877;
    margin-top:1%;
    border: 1px solid #fff;
    box-shadow: 0 0 1px #fff;
}

.back:hover{
    background-color:#FE7877;
    color:#313244;
    font-weight:bold;
}
