div.TimeStyle{
   
    clip-path: polygon( 0% 0%, 88% 0%, 632% 223%, 0% 97% );
          
          background-color: #FE7A7B;
          background-color: #fec8c8;
          background-color: #E3BF67;
          background-color: #313244;
    
}

p.certHead{
    color:#FE7877;
    text-align: center;
    margin-top:4%;
    margin-bottom:3%;
    font-size: 40px;
    
}

.vertical-timeline::before{
    background: #fff;
}

.vertical-timeline-element-content{
    color:#313244;
    background: #fff;
    
}

.vertical-timeline-element-content > p{
    color:#313244;
}

span.vertical-timeline-element-date{
    color: #fff !important;
    margin-left: 45px;
    margin-right: 45px;
}


@media only screen and (max-width: 1180px) {

    span.vertical-timeline-element-date{
        color: #333333 !important;
       
    }
    

  }